.topbar {
  -moz-transition: color 0.2s ease-out;
  -webkit-transition: color 0.2s ease-out;
  background: #ffffff;
  height: 50px;
  transition: color 0.2s ease-out;
  position: relative;
  display: flex;
  &::after {
    clear: both;
    content: '';
    display: block;
  }
  .header-left {
    display: flex;
    .topnav {
      height: 50px;
      margin: 0;
      display: flex;
      .menutoggle {
        background: none;
        color: #2b2e33;
        cursor: pointer;
        float: left;
        font-size: 22px;
        height: 50px;
        position: relative;
        text-decoration: none !important;
        width: 50px;
        i {
          display: inline-block;
          margin: 14px 0 0 13px;
          &:before {
            -webkit-font-smoothing: antialiased;
            content: "r";
            font-family: "line-icons" !important;
            font-style: normal !important;
            font-variant: normal !important;
            font-weight: normal !important;
            line-height: 1;
            speak: none;
            text-transform: none !important;
          }
        }
      }
      .menu__handle {
        background: rgba(0, 0, 0, 0);
        border: none;
        height: 24px;
        left: 18px;
        outline: none;
        padding: 0;
        position: absolute;
        top: 11px;
        width: 20px;
        z-index: 2000;
        &::before {
          -moz-transform-origin: 50% 50%;
          -moz-transform: translate3d(0, -5px, 0);
          -webkit-transform-origin: 50% 50%;
          -webkit-transform: translate3d(0, -5px, 0);
          transform-origin: 50% 50%;
          transform: translate3d(0, -5px, 0);
          background: #2b2e33;
          content: '';
          height: 2px;
          left: 0;
          position: absolute;
          top: 50%;
          width: 100%;
          -webkit-transition: transform 0.3s linear;
          -moz-transition: transform 0.3s linear;
          -o-transition: transform 0.3s linear;
          -ms-transition: transform 0.3s linear;
          transition: transform 0.3s linear;
        }
        &::after {
          -moz-transform-origin: 50% 50%;
          -moz-transform: translate3d(0, 5px, 0);
          -webkit-transform-origin: 50% 50%;
          -webkit-transform: translate3d(0, 5px, 0);
          background: #2b2e33;
          content: '';
          height: 2px;
          left: 0;
          position: absolute;
          top: 50%;
          transform-origin: 50% 50%;
          transform: translate3d(0, 5px, 0);
          width: 100%;
          -webkit-transition: transform 0.3s linear;
          -moz-transition: transform 0.3s linear;
          -o-transition: transform 0.3s linear;
          -ms-transition: transform 0.3s linear;
          transition: transform 0.3s linear;
        }
        span {
          height: 2px;
          left: 0;
          overflow: hidden;
          position: absolute;
          text-indent: 200%;
          top: 12px;
          -webkit-transition: opacity 0.25s linear;
          -moz-transition: opacity 0.25s linear;
          -o-transition: opacity 0.25s linear;
          -ms-transition: opacity 0.25s linear;
          transition: opacity 0.25s linear;
          width: 100%;
        }
      }
      .mmenu {
        .dropdown {
          a {
            color: #656565;
          }
          position: static;
        }
        .dropdown-menu {
          border: 1px solid #efefef;
          box-shadow: none;
          left: auto;
          margin-top: 0 !important;
          padding: 0;
          a {
            color: #656565;
          }
          .withoutdesc {
            display: block;
            margin-top: 0;
            padding: 15px 20px;
            text-align: left;
            text-transform: none;
            width: 100%;
            ul {
              li {
                padding: 3px 10px;
                &:hover {
                  background-color: #f5f5f5 !important;
                  color: #262626;
                  text-decoration: none;
                }
                &:focus {
                  background-color: #f5f5f5 !important;
                  color: #262626;
                  text-decoration: none;
                }
              }
            }
            li {
              &:last-child {
                border-bottom: 0 solid #fff;
              }
            }
          }
          > li {
            > a {
              padding: 6px 15px;
            }
          }
        }
        .navbar-nav {
          > li {
            > .dropdown-menu {
              margin-top: 1px;
            }
          }
        }
        i {
          color: #BFBFBF;
        }
        .form-control {
          border: 1px solid #efefef;
          margin-top: 10px;
        }
        .btn {
          margin: 10px 0 20px;
        }
        video {
          height: auto;
          max-width: 100%;
        }
        iframe {
          max-width: 100%;
        }
        embed {
          max-width: 100%;
        }
        object {
          max-width: 100%;
        }
        a {
          &:hover {
            text-decoration: none;
          }
        }
        .mmenu-content.withdesc {
          a {
            &:after {
              color: #CFCFCF;
              content: attr(data-description);
              display: block;
              font-size: 11px;
              font-weight: 400;
              line-height: 0;
              margin: 10px 0 15px;
              text-transform: uppercase;
            }
          }
        }
        .dropdown-submenu {
          position: relative;
          &:hover {
            > .dropdown-menu {
              display: block;
            }
            > a {
              after {
                border-left-color: #fff;
              }
            }
          }
          > .dropdown-menu {
            left: 100%;
            margin-left: 0;
            margin-top: 0 !important;
            top: 0;
            -webkit-border-radius: 0 6px 6px 6px;
            -moz-border-radius: 0 6px 6px 6px;
            border-radius: 0 6px 6px 6px;
            -moz-background-clip: padding;
            -webkit-background-clip: padding-box;
            background-clip: padding-box;
          }
          > a {
            &:after {
              border-color: transparent;
              border-left-color: #cccccc;
              border-style: solid;
              border-width: 5px 0 5px 5px;
              content: " ";
              display: block;
              float: right;
              height: 0;
              margin-right: -2px;
              margin-top: 5px;
              width: 0;
            }
          }
        }
        .dropdown-submenu.pull-left {
          float: none;
          > .dropdown-menu {
            left: -100%;
            margin-left: 10px;
            -webkit-border-radius: 6px 0 6px 6px;
            -moz-border-radius: 6px 0 6px 6px;
            border-radius: 6px 0 6px 6px;
            -moz-background-clip: padding;
            -webkit-background-clip: padding-box;
            background-clip: padding-box;
          }
        }
        p {
          color: #656565;
          font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
          font-size: 13px;
        }
        .half {
          left: auto !important;
          right: auto !important;
          width: 50%;
          float: none;
          text-align: left;
        }
        .container {
          position: relative;
        }
        .nav.navbar-right {
          .dropdown-menu {
            left: auto;
            right: 0;
          }
        }
        .mmenu-content {
          padding: 15px 25px;
        }
        .dropdown.mmenu-fw {
          .dropdown-menu {
            left: 0;
            margin-top: 0 !important;
            right: 0;
            -webkit-box-shadow: 0 3px 3px rgba(0, 0, 0, 0.18);
            -moz-box-shadow: 0 3px 3px rgba(0, 0, 0, 0.18);
            box-shadow: 0 3px 3px rgba(0, 0, 0, 0.18);
          }
        }
        .title {
          border-bottom: 1px solid #efefef;
          font-size: 14px;
          margin-top: 15px;
          padding-bottom: 10px;
          text-transform: uppercase;
          strong {
            font-weight: 900;
          }
        }
        ul {
          list-style: none;
        }
        .nav {
          position: static;
        }
        .collapse {
          position: static;
        }
        .dropup {
          position: static;
        }
