              color: #298598;
              padding: 13px 19px 13px;
              &::before {
                -moz-transform: scale(1);
                -webkit-transform: scale(1);
                transform: scale(1);
                width: 100%;
              }
            }
            &:focus {
              &::before {
                -moz-transform: scale(1);
                -webkit-transform: scale(1);
                transform: scale(1);
                width: 100%;
              }
            }
