            &::before {
              -moz-transform: scale(1);
              -webkit-transform: scale(1);
              transform: scale(1);
              width: 100%;
            }
          }
        }
        > li.open {
          > a {
            &::before {
              -moz-transform: scale(1);
              -webkit-transform: scale(1);
              transform: scale(1);
              width: 100%;
            }
          }
        }
      }
      > ul.nav-icons {
        > li {
          > a {
